.page {
    margin-bottom: 3rem;
}

.search-header {
    width: 100%;
    height: 2.5rem;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 10;
    background: linear-gradient(rgba(1, 1, 1, 0.2) 0%, rgba(255, 255, 255, 0) 100%);
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
}

.search-header.red-bg {
    background: linear-gradient(rgb(224, 188, 121) 0%, rgba(80, 145, 219, 0) 100%)
}

.search-header .classify-icon {
    width: 1.2rem;
    height: 1.2rem;
    background-image: url("../../../images/common/class.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 4%;
}

.search-header .search-wrap {
    width: 70%;
    height: 1.3rem;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 5px;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -webkit-align-items: center;
}

.search-header .search-wrap .search-icon {
    width: 1rem;
    height: 1rem;
    background-image: url("../../../images/common/search.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
}

.search-header .search-wrap .search-text {
    width: 80%;
    font-size: 0.6rem;
    color: #ffffff;
}

.search-header .login-wrap {
    width: auto;
    height: auto;
    margin-right: 4%;
}

.search-header .login-wrap .login-text {
    font-size: 0.8rem;
    color: #FFFFFF;
}

.search-header .login-wrap .my{
    width: 1.2rem;
    height: 1.2rem;
    background-image: url("../../../images/home/index/my.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
}

.swiper-wrap {
    width: 100%;
    height: 9.1rem;
    position: relative;
    z-index: 1;
}

.swiper-wrap img {
    width: 100%;
    height: 100%;
}

.quick-nav {
    width: 100%;
    height: 4rem;
    background: #FFFFFF;
    margin-top: 0.5rem;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
}

.quick-nav .item {
    width: 3.5rem;
    height: auto;
    margin-left: 2%;
    margin-right: 2%;
}

.quick-nav .item-img {
    width: 2rem;
    height: 2rem;
    margin: 0 auto;
}

.quick-nav .item-img img {
    width: 100%;
    height: 100%
}

.quick-nav .item-text {
    width: 100%;
    text-align: center;
    font-size: 0.7rem;
    color: #7b7f82;
    margin-top: 0.2rem;
}

.goods-level-wrap {
    width: 100%;
    background-color: #FFFFFF;
    margin-top: 0.5rem;
}

.goods-level-wrap .classify-title {
    width: 100%;
    font-size: 0.7rem;
    height: 1.6rem;
    border-bottom: solid #EFEFEF 1px;
    text-align: center;
    line-height: 1.6rem;
}

.goods-level-wrap .classify-title.color1 {
    color: #F73B61
}

.goods-level-wrap .classify-title.color2 {
    color: #FE6719
}

.goods-level-wrap .classify-title.color3 {
    color: #5FC600
}

.goods-level-wrap .goods-level1-wrap {
    width: 100%;
    height: 7rem;
    display: flex;
    display: -webkit-flex;
    overflow: hidden;
    border-bottom: solid #EFEFEF 1px;
}

.goods-level-wrap .goods-level1-item0 {
    width: 50%;
    height: 100%;
    border-right: solid #EFEFEF 1px;
    position: relative;
    z-index: 1;
}

.goods-level-wrap .goods-level1-item0 .goods-title2 {
    width: 98%;
    font-size: 0.7rem;
    font-weight: bold;
    overflow: hidden;
    height: 1rem;
    text-align: center;
    margin: 0 auto;
    margin-top: 0.5rem;
}

.goods-level-wrap .goods-level1-item0 .goods-text2 {
    width: auto;
    font-size: 0.6rem;
    color: #7b7f82;
    margin-top: 0.2rem;
    text-align: center;
}

.goods-level-wrap .goods-level1-item0 .goods-img2 {
    width: 3rem;
    height: 3.5rem;
    margin: 0 auto;
    margin-top: 0.3rem;
}

.goods-level-wrap .goods-level1-item0 .goods-img2 img {
    width: 100%;
    height: 100%;
}

.goods-level-wrap .goods-level1-item1 {
    width: 49.5%;
    height: 100%;
}

.goods-level-wrap .goods-level1-wrap .goods-title {
    width: 95%;
    height: 1rem;
    font-size: 0.7rem;
    overflow: hidden;
    font-weight: bold;
    position: absolute;
    z-index: 1;
    left: 0.2rem;
    top: 0.5rem;
}

.goods-level-wrap .goods-level1-wrap .goods-text {
    width: 3rem;
    font-size: 0.6rem;
    color: #CB385D;
    position: absolute;
    left: 0.2rem;
    top: 1.5rem;
}

.goods-level-wrap .goods-level1-wrap .goods-price1 {
    width: auto;
    height: 1rem;
    background: #F21D4F;
    color: #FFFFFF;
    font-size: 0.7rem;
    text-align: center;
    line-height: 1rem;
    position: absolute;
    left: 4.5rem;
    top: 1.5rem;
    border-radius: 10px;
}

.goods-level-wrap .goods-level1-wrap .goods-price3 {
    width: auto;
    height: 1rem;
    background: #5FC600;
    color: #FFFFFF;
    font-size: 0.7rem;
    text-align: center;
    line-height: 1rem;
    position: absolute;
    left: 4.5rem;
    top: 1.5rem;
    border-radius: 10px;
}

.goods-level-wrap .goods-level1-wrap .goods-img {
    width: 7.5rem;
    height: 3.7rem;
    margin: 0 auto;
    margin-top: 2.7rem;
}

.goods-level-wrap .goods-level1-wrap .goods-img img {
    width: 100%;
    height: 100%;
}

.goods-level-wrap .goods-level1-item1 .goods-row {
    width: 100%;
    height: 49.5%;
    border-bottom: solid #EFEFEF 1px;
    position: relative;
    z-index: 1;
}

.goods-level-wrap .goods-level1-item1 .goods-row .goods-row-title {
    width: 60%;
    height: 1rem;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    left: 0.2rem;
    top: 0.5rem;
    font-size: 0.7rem;
    font-weight: bold;
}

.goods-level-wrap .goods-level1-item1 .goods-row .goods-row-text {
    width: auto;
    height: 1rem;
    position: absolute;
    z-index: 1;
    left: 0.2rem;
    top: 1.5rem;
    font-size: 0.6rem;
    color: #7b7f82
}

.goods-level-wrap .goods-level1-item1 .goods-row .goods-row-img {
    width: 3rem;
    height: 3rem;
    position: absolute;
    z-index: 1;
    right: 0.6rem;
    top: 0.2rem;
}

.goods-level-wrap .goods-level1-item1 .goods-row .goods-row-img img {
    width: 100%;
    height: 100%;
}

.goods-level-wrap .goods-list-wrap {
    width: 100%;
    display: flex;
    display: -webkit-flex;
}

.goods-level-wrap .goods-list-wrap .goods-list {
    width: 25%;
    height: 7.6rem;
    border-right: solid 1px #EFEFEF
}

.goods-level-wrap .goods-list .title {
    width: 98%;
    height: 1rem;
    font-size: 0.7rem;
    margin: 0 auto;
    overflow: hidden;
    text-align: center;
    font-weight: bold;
    margin-top: 0.5rem;
}

.goods-level-wrap .goods-list .image {
    width: 3.8rem;
    height: 3.8rem;
    margin: 0 auto;
}

.goods-level-wrap .goods-list .image img {
    width: 100%;
    height: 100%;
    margin-top: 0.2rem;
}

.goods-level-wrap .goods-list .price {
    width: 100%;
    text-align: center;
    color: #D32A4E;
    font-size: 0.7rem;
    margin-top: 0.4rem;
}

.goods-level-wrap .goods-list .unprice {
    width: 100%;
    text-align: center;
    color: #7b7f82;
    font-size: 0.6rem;
    margin-top: 0rem;
    text-decoration: line-through;
}

.reco-title-wrap {
    width: 100%;
    height: 2.5rem;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    -webkit-align-items: center;
}

.reco-title-wrap .line {
    width: 35%;
    background-color: #D4D4D4;
    height: 1px;
}

.reco-title-wrap .reco-text-wrap {
    width: 6rem;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    -webkit-justify-content: center;
}

.reco-title-wrap .reco-text-wrap .reco-icon {
    width: 1rem;
    height: 1rem;
    background-image: url('../../../images/home/index/recom.png');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 0.3rem;
}

.reco-title-wrap .reco-text-wrap .reco-text {
    font-size: 0.8rem;
}

.reco-item-wrap {
    width: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap
}

.reco-item-wrap .reco-item {
    width: 46%;
    height: 11rem;
    background: #FFFFFF;
    margin-bottom: 0.5rem;
    margin-left: 2%;
    margin-right: 2%;
}

.reco-item-wrap .reco-item .image {
    width: 7rem;
    height: 7rem;
    margin: 0 auto;
    margin-top: 0.5rem;
}

.reco-item-wrap .reco-item .image img {
    width: 100%;
    height: 100%;
}

.reco-item-wrap .reco-item .title {
    width: 100%;
    height: 2rem;
    overflow: hidden;
    font-size: 0.7rem;
    margin-top: 0.2rem;
}

.reco-item-wrap .reco-item .price {
    font-size: 0.7rem;
    color: #D32A4E
}
